import React from 'react';
import { ImageTimeAnimation } from './animation';

export default function BasicRisu({ name, x, y, center, width, tint, onLoad }) {
  let style = {};
  if (center) {
    style = {display: 'block', margin: '0 auto'};
  }
  else {
    style = {display: 'inline-block', position: 'relative', marginLeft: x || 0, marginTop: y || 0};
  }
  if (width) {
    style = {...style, width, height: 'auto'}
  }
  if (tint) {
    const [ brightness, hue ] = tint;
    style = {...style, filter: `brightness(${brightness}) hue-rotate(${hue}deg)`};
  }
  return (
    <ImageTimeAnimation
      alt="matsurisu"
      style={style}
      frameDur={5}
      onLoad={onLoad}
      srcs={[`/matsurisu/static/img/risu/${name}1.png`, `/matsurisu/static/img/risu/${name}2.png`]}
    />
  );
}
