import React, { useContext } from 'react';
import { AnimationContext, ContainerContext } from './scene';

function getFrame(counter, frameDur, numFrames) {
  frameDur = frameDur || 1;
  return Math.floor(counter / frameDur) % numFrames;
}

export function ImageAnimation({ control, frameDur, srcs, alt, style, onLoad }) {
  const i = getFrame(control, frameDur, srcs.length);
  return (
    <img alt={alt} style={style} src={srcs[i]} onLoad={onLoad} />
  );
}

export function ImageTimeAnimation({ frameDur, srcs, alt, style, onLoad }) {
  const frame = useContext(AnimationContext);
  return <ImageAnimation control={frame} {...{ frameDur, srcs, alt, style, onLoad }} />
}

export function ImagePosAnimation({ frameDur, srcs, alt, style, onLoad }) {
  const { y } = useContext(ContainerContext);
  return <ImageAnimation control={y} {...{ frameDur, srcs, alt, style, onLoad }} />
}