import React, { useRef, useLayoutEffect, useState, useContext, useCallback } from 'react';
import { ContainerContext } from './scene';

export default function Parallax({ position, offset, stay, factor, children }) {
  const ref = useRef(null);
  const containerDims = useContext(ContainerContext);
  const [height, setHeight] = useState(0);

  position = position || 0;
  offset = offset || 0;
  stay = (stay === undefined) ? 300 : stay;
  const delta = (containerDims.y + position) * factor;
  const top = Math.max(Math.min(delta + stay * factor, 0), delta) + (containerDims.height / 2) + offset;
  const zIndex = Math.floor(100 * factor);
  const hide = (top - 2 * height > containerDims.height || (top + 2 * height) < 0);

  const updateHeight = useCallback(() => {
    if (!ref.current) return;
    setHeight(ref.current.getBoundingClientRect().height);
  }, [ref, setHeight]);

  useLayoutEffect(updateHeight, [updateHeight]);

  return (
    (hide)
    ? null
    : <div ref={ref} className="parallax" style={{left: 0, top, zIndex}}>
        {React.Children.map(children, (el) => React.cloneElement(el, { onLoad: updateHeight }) )}
      </div>
  );
}
